import { useCallback } from 'react'
import axios from "axios"

export const useAnnouncementContent = (_locale: string) => {
  const handleAnnouncementContent = useCallback(async () => {
    try {
      const baseUrl = `${process.env.REACT_APP_MUSASHIBO_LINK}/api/frontend/get-announcement`

      const response = await axios.get(baseUrl, {
        params: {
          language: _locale
        }
      })

      const {data} = await response?.data
      return data
    } catch (e) {
      return {}
    }
  }, [_locale])

  return { onAnnouncementContent: handleAnnouncementContent }
}