import React, { useCallback } from 'react'
import BigNumber from 'bignumber.js'
import { CHAIN_ID } from 'config/constants/chains'
import tokens from 'config/constants/tokens'
import { useAccountTokenBalanceCallback } from 'hooks/useTokenBalance'
import { getErc20TokenContract } from 'utils/contractHelperMulti'
import { usePotTotalDeposit } from './usePotTotalDeposit'

export const usePotTotalReward = (potAddress: string, chainId: number) => {
//   const { onAccountTokenBalance : handleAccountTimeTokenBalance } = useAccountTokenBalanceCallback(tokens.time.address[chainId])
//   const { onAccountTokenBalance : handleAccountMemoTokenBalance } = useAccountTokenBalanceCallback(tokens.memo.address[chainId])
  
  const { onPotTotalDeposit: handlePotTotalDeposit } = usePotTotalDeposit(potAddress, chainId)

  const handlePotTotalReward = useCallback(async () => {
      try {
        // Avax price calculation: (MemoToken.balance(jackpot) + (TIME in jackpot)) - (Jackpot total deposit)
        if (chainId === CHAIN_ID.AVAX) {
          const timeContract = getErc20TokenContract(tokens.time.address[chainId], CHAIN_ID.AVAX)
          const memoContract = getErc20TokenContract(tokens.memo.address[chainId], CHAIN_ID.AVAX)
          
          const timeContractBalance = await timeContract.balanceOf(potAddress)
          const timeContractBalanceBn = new BigNumber(timeContractBalance.toString())
          const memoContractBalance = await memoContract.balanceOf(potAddress)
          const memoContractBalanceBN = new BigNumber(memoContractBalance.toString())

        //   const test = await handleAccountTimeTokenBalance(potAddress)
        //   const test2 = await handleAccountMemoTokenBalance(potAddress)

          const potTotalDepositResult = await handlePotTotalDeposit()
          const potTotalDepositBN = new BigNumber(potTotalDepositResult.toString())
          const prizeBN = memoContractBalanceBN.plus(timeContractBalanceBn).minus(potTotalDepositBN)
    
          return {
            totalReward: prizeBN,
            totalDeposit: potTotalDepositBN
          }
        }
        return {
            totalReward: new BigNumber("0"),
            totalDeposit: new BigNumber("0")
        }
      } catch (e) {
          console.error(e)
          return false
      }
  }, [chainId, potAddress, handlePotTotalDeposit])

  return { onPotTotalReward : handlePotTotalReward }
}