import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import BigNumber from 'bignumber.js'
import styled from 'styled-components'
import { useTranslation } from 'contexts/Localization'
import { useNetworkChainId } from 'state/hooks'
import { Flex, CardFooter, ExpandableLabel, HelpIcon, useTooltip, Button, Box } from '@pancakeswap/uikit'
import { MultiPotConfig } from 'config/constants/types'
import { CompoundingPoolTag, ManualPoolTag } from 'components/Tags'
import Column, { AutoColumn } from '../../../../../components/Layout/Column'
import ExpandedFooter from './ExpandedFooter'

interface FooterProps {
  pot: MultiPotConfig
  account: string
  totalCakeInVault?: BigNumber
}

const ExpandableButtonWrapper = styled(Flex)`
  align-items: center;
  justify-content: space-between;
  button {
    padding: 0;
  }
`

const DepositButton = styled(Button)`
  background-color: #910101;
  border-radius: 5px;
  font-size: 25px;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 100%;
  width: 100%;

  ${({ theme }) => theme.mediaQueries.md} {
    padding-top: 0px;
    padding-bottom: 0px;
    height: 48px;
    width: 70%;
  }
`

const ViewPDButton = styled(Button)`
  background-color: transparent;
  border-radius: 5px;
  font-size: 16px;
  color: #910101;
  box-shadow: none;
  font-weight: bold;
  white-space: normal;
`

const Footer: React.FC<FooterProps> = ({ pot, account }) => {
  const isAutoVault = false
  const { potId, potAddress, isClosed = false } = pot
  const { t } = useTranslation()
  const [isExpanded, setIsExpanded] = useState(false)
  const chainId = useNetworkChainId()

  const manualTooltipText = t('You must harvest and compound your earnings from this pool manually.')
  const autoTooltipText = t(
    'Any funds you stake in this pool will be automagically harvested and restaked (compounded) for you.',
  )

  const { targetRef, tooltip, tooltipVisible } = useTooltip(isAutoVault ? autoTooltipText : manualTooltipText, {
    placement: 'bottom',
  })

  return (
    <CardFooter style={{ borderTop: 'none', paddingTop: '0px' }}>
      <AutoColumn gap="1px">
        <Box mt="1rem" className="text-center">
          <Link to={`/pot/${potAddress[chainId]}`}>
            <DepositButton>
              {isClosed === false? t(`DEPOSIT ${pot.stakingToken.symbol}`) : t('VIEW POOL')}
            </DepositButton>
          </Link>
        </Box>
        <Box  className="text-center">
          <Link to={`/pot/${potAddress[chainId]}`}>
            <ViewPDButton >
              {isClosed === false? t('View Pool Distribution') : t('This pool has ended and ready for withdrawal')}
            </ViewPDButton>
          </Link>
        </Box>
      </AutoColumn>
    </CardFooter>
  )
}

export default Footer


