const PotBanner: React.FC<PotBannerProps> = ({ pot }) => {
  const { t } = useTranslation()
  const avatar1 = <Avatar src={getImageUrlFromToken(pot.stakingToken)} alt="pot avatar 1" />
  const avatar2 = <Avatar src={getImageUrlFromToken(pot.stakingToken)} alt="pot avatar 2" />
  const avatar3 = <Avatar src={getImageUrlFromToken(pot.stakingToken)} alt="pot avatar 3" />

  return (
    <StyledBannerCard>
      <Flex alignItems="center">
        <MobileBanner>
            <img src={pot.potBannerPath} width="300" height="192" alt="" />
        </MobileBanner>
      </Flex>
      <Flex>
        <Banner>
            <img src={pot.potBannerPath} width="400" height="192" alt="" />
        </Banner>
      </Flex>
    </StyledBannerCard>
  )
}

export default PotBanner

