// CMS interface
type cmsBaseObj = {
  content: string,
  language: string,
  page: string,
  key: string
}
type cmsObj = {
  banner?: cmsBaseObj,
  jackpot?: cmsBaseObj
}

const Pot: React.FC = () => {
  const { t, currentLanguage } = useTranslation()
  const { account } = useWeb3React()
  const chainId = useNetworkChainId()

  // cms content
  const { onCmsContent: handleCmsContent } = useCmsContent("jackpot", currentLanguage.locale)
  const [cmsContent, setCmsContent] = useState(() => {
    const init: cmsObj = {
      banner: {
        content: "",
        language: "",
        page: "",
        key: ""
      },
      jackpot: {
        content: "",
        language: "",
        page: "",
        key: ""
      }
    }
    return init
  })
  const [announcementContent, setAnnouncementContent] = useState(() => {
    const init: announcementObj = {
      label: "",
      content: "",
      language: ""
    }
    return init
  })
  const announcementContentRef = useRef(announcementContent)
  const { onAnnouncementContent: handleAnnouncementContent } = useAnnouncementContent(currentLanguage.locale)
  const [onAnnouncement] = useModal(<AnnouncementModal title={announcementContent.label} content={announcementContent.content} />)
  const isEmptyRef = useRef(true)
  const isOpenedRef = useRef(false)

  useEffect(() => {
    const getCms = async () => {
      const result = await handleCmsContent()
      if (Object.keys(result).length > 0) {
        setCmsContent(result)
      }
    }
    const getAnnouncement = async () => {
      const result = await handleAnnouncementContent()
      // const isEmpty = Object.values(object).every(x => (x === null || x === ''));
      if (Object.keys(result).length > 0) {
        setAnnouncementContent(result)
        announcementContentRef.current = result
      }
      const isEmpty = Object.values(announcementContentRef.current).every(x => (x === null || x === ''));
      isEmptyRef.current = isEmpty
    }
    getCms()
    // getAnnouncement()
  }, [handleAnnouncementContent, handleCmsContent])

  if (isEmptyRef.current === false && isOpenedRef.current === false) {
    onAnnouncement()
    isOpenedRef.current = true
  }

  // const CardLayout = styled(FlexLayout)`
  //   justify-content: center;
  // `
  const filteredPots = pots.filter((pot) => pot.potAddress[chainId] && pot.potAddress[chainId] !== "")

  const tableLayout = <PotsTable pots={filteredPots} account={account} userDataLoaded={false} cmsContent={cmsContent.jackpot.content} />

  // card
  // const cardLayout = (
  //   <CardLayout>
  //     {pots.map((pot) =>
  //       <PotPairCard key={pot.potId} pot={pot} account={account} cmsContent={cmsContent.jackpot.content} />
  //     )}
  //   </CardLayout>
  // )

  return (
    <>
      <Banner content={cmsContent.banner.content} />
      <AppBody>
        {tableLayout}
      </AppBody>
    </>
  )
}

export default Pot
