import { useCallback, useEffect, useState } from 'react'
import axios from "axios"

export const useCmsContent = (_page: string, _locale: string) => {
  const handleCmsContent = useCallback(async () => {
    try {
      const baseUrl = `${process.env.REACT_APP_MUSASHIBO_LINK}/api/frontend/get-content`

      const response = await axios.get(baseUrl, {
        params: {
          page    : _page,
          language: _locale
        }
      })

      const {data} = await response?.data
      return data
    } catch (e) {
      return {}
    }
  }, [_locale, _page])

  return { onCmsContent: handleCmsContent }
}